.map_container{
    height: 100vh;
    width: 80vw;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .map{
        margin-bottom: 20px;
    }
    .white{
        color: rgb(216, 216, 216);
        font-weight: 100;
        text-align: left;
        width: 100%;
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        margin: 3px 0;
        strong{
            color: rgb(68, 68, 68);
            padding-right: 3px;
        }
    }
    .phone{
        margin: 10px 0;
        width: 100%;
    }
    .phone_max{
        margin-top: 20px;
        width: 100%;
    }
    .ex{
        color: white;
        font-size: calc(8px + 1vh);
        font-weight: bold;
    }
    .small{
        font-size: calc(4px + 1vh);
    }
    .ymaps-2-1-79-map{
        border-radius: 20px !important;
    }
    a{
        color: #fff !important;
        display: flex;
        margin-top: 35px;
        background: rgba(59, 59, 59, 0.548);
        padding: 5px 20px;
        border-radius: 20px !important;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
        p{
            padding-bottom: 2px;
            font-size: calc(6px + 1vh);
        }
    }
}