.container{
    width: 45vw;
    background: transparent;
    transition: ease-in-out 0.5s;
    .transpanent_block{
        background: transparent;
        height: 100px;
        width: 100%;
    }
    .header{
        font-size: 25px;
        font-weight: 700;
        color: rgb(90, 90, 90);
        // font-style: italic;
    }
    .text_container{
        padding: 70px 0;
        color: rgb(66, 66, 66);
        img{
            width: 100%;
            height: 450px;
            margin: 50px 0;
            object-fit: cover;
        }
        .title{
            font-size: calc(6px + 1vh);
        }
        .row{
            margin-top: 120px;
            width: 100%;
            display: flex;
            justify-content: space-between;
            .left{
                width: 60%;
                video{
                    width: 100%;
                    border-radius: 10px;
                    border: none;
                    margin-bottom: 100px;
                }
            }
            .right{
                width: 35%;
                display: flex;
                justify-content: flex-start;
                align-items: flex-start;
                flex-direction: column;
                #diadoc{
                    width: 55%;
                }
                #diadoc_p {
                    margin-top: 0;
                    padding-left: 10px;
                    margin-bottom: 5px;
                }
                img{
                    width: 50%;
                    margin: 0;
                    height: auto;
                    object-fit: contain;
                }
                p{
                    margin-top: 15px;
                    padding-left: 10px;
                }
            }
        }
    }   
}

.row_map{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 30px 0;
    #first{
        background: linear-gradient(to top left, powderblue, pink);
    }
    #second{
        background: linear-gradient(45deg, #EECFBA, #C5DDE8);
    }
    .map_item{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 400px;
        box-sizing: border-box;
        border-radius: 15px;
        // background: rgb(238, 238, 238);
        // border: 1px linear-gradient( from left to right, transparent, black);
        
        color: rgb(82, 82, 82);
        .collumn{
            display: flex;
            height: auto;
            flex-direction: column;
            justify-content: space-between;
            padding-bottom: calc(3vh + 5px);
            width: calc(90% - 10px);
            margin: 0 auto;
            p{
                font-size: 14px;
                margin: 3px 0;
            }
        }
        
    }
    .ymaps-2-1-79-events-pane .ymaps-2-1-79-inner-panes .ymaps-2-1-79-map{
        border-radius: 20px !important;
    }
    .ymaps-2-1-79-map .ymaps .ymaps-2-1-79-map .ymaps-2-1-79-map{
        border-radius: 20px !important;
        &:before{
            border-radius: 20px !important;
        }
        &:after {
            border-radius: 20px !important;
        }
    }
}

#mobile{
    display: none;
    .row_item{
        display: flex;
        justify-content: space-between;
        width: 100%;
        height: 93px;
        margin: 20px 0;
        align-items: center;
        .left{
            min-width: 90px;
            max-width: 95px;
            height: 100%;
            display: flex;
            align-items: center;
            #vg{
                background: linear-gradient(146.45deg, #48D770 6.71%, #216B59 111.14%);
            }
            #vt{
                background: linear-gradient(146.45deg, #4867D7 6.71%, #21286B 111.14%);
            }
            #fe{
                background: linear-gradient(146.45deg, #D74848 6.71%, #6B2121 111.14%);
            }
            #os{
                background: linear-gradient(146.45deg, #48CED7 6.71%, #21506B 111.14%), #FFFFFF;
            }
            #none{
                background: linear-gradient(146.45deg, #7548D7 6.71%, #6B2164 111.14%), #FFFFFF;
                h2{
                    font-size: 15px
                }
            }
            .items_img{
                height: 77px;
                width: 77px;
                border-radius: 22px;
                background-color: white;
                border: none !important;
                margin: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
                h2{
                    font-weight: bold;
                    font-style: italic;
                    color: rgb(230, 230, 230);
                    font-size: 31px;
                }
            }
        }
        .right{
            width: 68%;
            border-bottom: 1px solid #2C2C2C;
            height: 100%;
            display: flex;
            align-items: center;
            .column_item{
                display: flex;
                flex-direction: column;
                height: 70%;
                .bold{
                    font-weight: 600;
                    font-size: 14px;
                    color: rgb(224, 224, 224);
                    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
                }
                .place{
                    font-weight: 400;
                    margin-top: 5px;
                    font-size: 12px;
                    color: rgb(172, 172, 172);
                    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
                }
                .phone{
                    font-weight: 400;
                    margin-top: 2px;
                    font-size: 12px;
                    color: rgb(172, 172, 172);
                    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
                }
            }
        }
    }
}

@media screen and (min-width: 900px) and (max-width: 1200px) {
    .container{
      width: 65vw;
    }
}

@media screen and (min-width: 345px) and (max-width: 899px) {
    #desktop{
        display: none !important;
    }
    #mobile{
        display: block !important;
    }
    .mobile{
        width: 80vw;
    }
}


